import React from "react";
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import "../css/carousel.css";
import FirstTruck from "../images/diesel-truck-1.jpeg";
import SecondTruck from "../images/diesel-truck-2.jpeg";
import ThirdTruck from "../images/diesel-truck-3.jpeg";

class CarouselComponent extends React.Component {
    render() {
        return (
            <div className="carouselWrapper">
                <Carousel showThumbs={false}>
                    <div>
                        <img className="img-fluid responsive-image" src={FirstTruck} alt="First truck" />
                    </div>
                    <div>
                        <img className="img-fluid responsive-image" src={SecondTruck} alt="Second truck" />
                    </div>
                    <div>
                        <img className="img-fluid responsive-image" src={ThirdTruck} alt="Third truck" />
                    </div>
                </Carousel>
            </div>
        )
    }
}

export default CarouselComponent;