import React from "react";
import Header from "../header";
import Footer from "../footer";
import axios from 'axios';
import "../css/order.css";
import "../css/label-button.css";
import SimpleReactValidator from 'simple-react-validator';
import DatePicker from 'react-datepicker';
import { AppConfig } from '../GlobalConstants';
import { Helmet } from "react-helmet";
import { IsUserAuthenticated, AuthenticatedUserId } from "../components/UserAuthentication";
import { withRouter } from 'react-router-dom';
import "react-datepicker/dist/react-datepicker.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as orderActions from '../actions/orderActions';
import PropTypes from 'prop-types';

class Order extends React.Component {
  constructor() {
    super();

    this.validator = new SimpleReactValidator({
      autoForceUpdate: this,
      className: 'text-danger'
    });
    this.deliveryClick = this.deliveryClick.bind(this);
    this.onSiteClick = this.onSiteClick.bind(this);
    this.quantityChangeHandler = this.quantityChangeHandler.bind(this);
    this.collectionDateChangeHandler = this.collectionDateChangeHandler.bind(this);
    this.vehicleRegistrationChangeHandler = this.vehicleRegistrationChangeHandler.bind(this);
    this.deliveryAddressChangeHandler = this.deliveryAddressChangeHandler.bind(this);
    this.deliveryDateChangeHandler = this.deliveryDateChangeHandler.bind(this);
    this.contactPersonChangeHandler = this.contactPersonChangeHandler.bind(this);
    this.collectingPersonChangeHandler = this.collectingPersonChangeHandler.bind(this);
    this.contactPersonCellNumberChangeHandler = this.contactPersonCellNumberChangeHandler.bind(this);
    this.contactPersonEmailChangeHandler = this.contactPersonEmailChangeHandler.bind(this);
    this.calculateTotalPrice = this.calculateTotalPrice.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
  }

  componentDidMount() {
    if (!AuthenticatedUserId) {
      this.props.history.push('/login');
    }
  }

  quantityChangeHandler(event) {
    const quantity = event.target.value;
    this.props.dispatch({
      type: "SET_QUANTITY",
      payload: quantity
    });
  }

  collectionDateChangeHandler(collectionDate) {
    this.props.dispatch({
      type: "SET_COLLECTION_DATE",
      payload: collectionDate
    });
  }

  vehicleRegistrationChangeHandler(event) {
    const registration = event.target.value;
    this.props.dispatch({
      type: "SET_COLLECTING_VEHICLE",
      payload: registration
    });
  }

  deliveryAddressChangeHandler(event) {
    const deliveryAddress = event.target.value;
    this.props.dispatch({
      type: "SET_DELIVERY_ADDRESS",
      payload: deliveryAddress
    });
  }

  deliveryDateChangeHandler(deliveryDate) {
    this.props.dispatch({
      type: "SET_DELIVERY_DATE",
      payload: deliveryDate
    });
  }

  contactPersonChangeHandler(event) {
    const contactPerson = event.target.value;
    this.props.dispatch({
      type: "SET_CONTACT_PERSON",
      payload: contactPerson
    });
  }

  collectingPersonChangeHandler(event) {
    const collectingPerson = event.target.value;
    this.props.dispatch({
      type: "SET_COLLECTING_PERSON",
      payload: collectingPerson
    });
  }

  contactPersonCellNumberChangeHandler(event) {
    const contactPersonCellNumber = event.target.value;
    this.props.dispatch({
      type: "SET_CELLPHONE_NUMBER",
      payload: contactPersonCellNumber
    });
  }

  contactPersonEmailChangeHandler(event) {
    const contactPersonEmail = event.target.value;
    this.props.dispatch({
      type: "SET_CONTACT_EMAIL",
      payload: contactPersonEmail
    });
  }

  deliveryClick() {
    const delivery = this.props.displayDelivery;
    this.props.dispatch({
      type: "SET_DISPLAY_VALUES",
      payload: {
        displayDelivery: !delivery,
        displayOnSite: !delivery ? false : this.props.displayOnSite
      }
    });
  }

  onSiteClick() {
    const onSite = this.props.displayOnSite;
    this.props.dispatch({
      type: "SET_DISPLAY_VALUES",
      payload: {
        displayOnSite: !onSite,
        displayDelivery: !onSite ? false : this.props.displayDelivery
      }
    });
  }

  async calculateTotalPrice() {
    if (!this.props.quantity) {
      console.log("Quantity is empty");
    }

    else if (this.props.displayOnSite) {
      const price = parseFloat(this.props.quantity) * AppConfig.DieselPrice;
      this.props.dispatch({
        type: "SET_TOTAL_PRICE",
        payload: price
      });
    }

    else if (this.props.deliveryAddress) {
      const price = parseFloat(this.props.quantity) * AppConfig.DieselPrice;
      const distance = await axios.post(`${AppConfig.ServerBaseUrl}/calculations/distance`, {
        address: this.props.deliveryAddress
      }).then((response) => {
        const responceDistance = response.data.distance.replace(" km", "");
        const distancePrice = parseFloat(responceDistance) * AppConfig.DistancePrice;
        const finalPrice = distancePrice + price;
        this.props.dispatch({
          type: "SET_TOTAL_PRICE",
          payload: finalPrice
        });
      }).catch((error) => {
        console.log(error);
      });
    }
  }

  returnTwoDigitNumber(inputNumber) {
    return ("0" + inputNumber).slice(-2);
  }

  generateOrderNumber() {
    const currentDate = new Date();
    const randomDigit = this.returnTwoDigitNumber(Math.floor(Math.random() * 100) + 1);
    const dateMonth = this.returnTwoDigitNumber(currentDate.getMonth() + 1);
    const dateDay = this.returnTwoDigitNumber(currentDate.getDate());
    const dateHours = this.returnTwoDigitNumber(currentDate.getHours());
    const dateMinutes = this.returnTwoDigitNumber(currentDate.getMinutes());
    const dateSeconds = this.returnTwoDigitNumber(currentDate.getSeconds());
    const orderNumber = `${dateMonth}${dateDay}-${dateHours}${dateMinutes}-${dateSeconds}${randomDigit}`;
    this.props.dispatch({
      type: "SET_ORDER_NUMBER",
      payload: orderNumber
    });
  }

  async submitHandler(event) {
    event.preventDefault();
    if (this.validator.allValid()) {
      await this.generateOrderNumber();
      await this.calculateTotalPrice();

      const orderData = {
        quantity: this.props.quantity,
        orderMethod: this.props.displayOnSite ? 'onSite' : 'delivery',
        deliveryDate: this.props.deliveryDate,
        deliveryAddress: this.props.deliveryAddress,
        collectionDate: this.props.collectionDate,
        collectingVehicleRegistration: this.props.collectingVehicleRegistration,
        contactPersonName: this.props.contactPerson,
        collectingPerson: this.props.collectingPerson,
        contactPersonCellNumber: this.props.contactPersonCellNumber,
        contactPersonEmail: this.props.contactPersonEmail,
        customerId: AuthenticatedUserId !== '' ? AuthenticatedUserId : '0',
        totalPrice: this.props.totalPrice,
        orderNumber: this.props.orderNumber
      };

      await axios.post(`${AppConfig.ServerBaseUrl}/order`, orderData).then((response) => {
        const orderId = response.data.orderId;
        this.props.history.push(`/order/payment?id=${orderId}`);
      }).catch(() => {
        console.log("failed to send email");
      });

    } else {
      this.validator.showMessages();
    }
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>Order Diesel - {AppConfig.BasePageTitle}</title>
        </Helmet>

        <Header />

        <div className="container-wrapper">
          <div className="container">
            <div className="card">
              <h2 className="text-center">Order Diesel</h2>
              <div className="card-body">
                <form onSubmit={this.submitHandler}>
                  <div className="form-group">
                    <input type="text" className="form-control" id="quantity" placeholder="Quantity (in litres)" value={this.props.quantity} onChange={this.quantityChangeHandler} />
                    {this.validator.message('quantity', this.props.quantity, 'required|numeric')}
                  </div>
                  <div className="form-group labelWithButtons">
                    <label>Order Method</label>
                    <div className="btn-toolbar buttonWrapper" role="toolbar" aria-label="Toolbar with button groups">
                      <div className="btn-group mr-2" role="group" aria-label="First group">
                        <button type="button" className="btn btn-info" onClick={this.onSiteClick}>On Site Collection</button>
                      </div>
                      <div className="btn-group mr-2" role="group" aria-label="Second group">
                        <button type="button" className="btn btn-info" onClick={this.deliveryClick}>Delivery</button>
                      </div>
                    </div>
                    {!this.props.displayDelivery && !this.props.displayOnSite ? this.validator.message('order method', this.props.deliveryDate, 'required') : ""}
                  </div>
                  <div id="deliveryWrapper" style={{ display: this.props.displayDelivery ? 'block' : 'none' }}>
                    <div className="form-group labelWithButtons">
                      <label>Delivery Date</label>
                      <div className='buttonWrapper'>
                        <DatePicker selected={this.props.deliveryDate} name='deliveryDate' dateFormat="dd MMMM yyyy" value={this.props.deliveryDate} onChange={this.deliveryDateChangeHandler} className="form-control" />
                      </div>
                      {this.props.displayDelivery ? this.validator.message('delivery date', this.props.deliveryDate, 'required') : ""}
                    </div>
                    <div className="form-group">
                      <input type="text" className="form-control" id="address" placeholder="Delivery Address" value={this.props.deliveryAddress} onChange={this.deliveryAddressChangeHandler} />
                      {this.props.displayDelivery ? this.validator.message('delivery address', this.props.deliveryAddress, 'required|string') : ""}
                    </div>
                    <div className="form-group">
                      <input type="text" className="form-control" id="contactPerson" placeholder="Contact Person Name" value={this.props.contactPerson} onChange={this.contactPersonChangeHandler} />
                      {this.props.displayDelivery ? this.validator.message('contact person name', this.props.contactPerson, 'required|string') : ""}
                    </div>
                  </div>
                  <div id="onSiteCollectionWrapper" style={{ display: this.props.displayOnSite ? 'block' : 'none' }}>
                    <div className="form-group labelWithButtons">
                      <label>Collection Date</label>
                      <div className='buttonWrapper'>
                        <DatePicker selected={this.props.collectionDate} name='collectionDate' dateFormat="dd MMMM yyyy" value={this.props.collectionDate} onChange={this.collectionDateChangeHandler} className="form-control" />
                      </div>
                      {this.props.displayOnSite ? this.validator.message('collection date', this.props.collectionDate, 'required') : ""}
                    </div>
                    <div className="form-group">
                      <input type="text" className="form-control" id="vehicleRegistration" placeholder="Collecting Vehicle Registration" value={this.props.collectingVehicleRegistration} onChange={this.vehicleRegistrationChangeHandler} />
                      {this.props.displayOnSite ? this.validator.message('collecting vehicle registration', this.props.collectingVehicleRegistration, 'required|string') : ""}
                    </div>
                    <div className="form-group">
                      <input type="text" className="form-control" id="collectingPerson" placeholder="Collecting Person Name" value={this.props.collectingPerson} onChange={this.collectingPersonChangeHandler} />
                      {this.props.displayOnSite ? this.validator.message('collecting person name', this.props.collectingPerson, 'required|string') : ""}
                    </div>
                  </div>

                  <div className="form-group">
                    <input type="text" className="form-control" id="contactNumber" placeholder="Contact Person Cell Number" value={this.props.contactPersonCellNumber} onChange={this.contactPersonCellNumberChangeHandler} />
                    {this.validator.message('cell number', this.props.contactPersonCellNumber, 'required|phone')}
                  </div>

                  {
                    IsUserAuthenticated ? '' :
                      <div className="form-group">
                        <input type="email" className="form-control" id="contactEmail" placeholder="Contact Person Email" value={this.props.contactPersonEmail} onChange={this.contactPersonEmailChangeHandler} />
                        {this.validator.message('contact person email', this.props.contactPersonEmail, 'required|email')}
                      </div>
                  }

                  <div className="mx-auto">
                    <button type="submit" className="btn btn-primary text-right">Place Order</button></div>
                </form>
              </div>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    );
  }
}

Order.propTypes = {
  orderActions: PropTypes.object,
  orders: PropTypes.array
};

const mapStateToProps = (state) => {
  return {
    quantity: state.orderReducer.quantity,
    collectionDate: state.orderReducer.collectionDate,
    collectingVehicleRegistration: state.orderReducer.collectingVehicleRegistration,
    deliveryAddress: state.orderReducer.deliveryAddress,
    deliveryDate: state.orderReducer.deliveryDate,
    contactPerson: state.orderReducer.contactPerson,
    collectingPerson: state.orderReducer.collectingPerson,
    contactPersonCellNumber: state.orderReducer.contactPersonCellNumber,
    contactPersonEmail: state.orderReducer.contactPersonEmail,
    orderNumber: state.orderReducer.orderNumber,
    displayDelivery: state.orderReducer.displayDelivery,
    displayOnSite: state.orderReducer.displayOnSite,
    totalPrice: state.orderReducer.totalPrice
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch,
    orderActions: bindActionCreators(orderActions, dispatch)
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Order));