import React from 'react';
import Header from '../header';
import Footer from '../footer';
import { AppConfig } from '../GlobalConstants';
import { Helmet } from "react-helmet";

class ViewUsers extends React.Component {
    constructor() {
        super();
        this.state = { items: [] };
    }

    componentDidMount() {
        fetch(`${AppConfig.ServerBaseUrl}/users`).then(res => res.json()).then(res => {
            if (res && res.length > 0) {
                this.setState({ items: res });
            }
        });
    }

    render() {
        return (
            <div>
                <Helmet>
                    <title>Users | {AppConfig.BasePageTitle}</title>
                </Helmet>

                <Header />

                <div className="container-wrapper">
                    <h2>Users listing</h2>
                    <div className="container">
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <th scope="col">#</th>
                                    <th scope="col">Company Name</th>
                                    <th scope="col">Telephone</th>
                                    <th scope="col">Email address</th>
                                    <th scope="col">Address</th>
                                    <th scope="col">Contact person name</th>
                                    <th scope="col">Contact person cellphone</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.items.map(item => {
                                    return (
                                        <tr>
                                            <th scope="row">{item.Id}</th>
                                            <td>{item.CompanyName}</td>
                                            <td>{item.CompanyTelephoneNumber}</td>
                                            <td>{item.EmailAddress}</td>
                                            <td>{item.Address}</td>
                                            <td>{item.ContactPersonName}</td>
                                            <td>{item.ContactPersonCellphone}</td>
                                        </tr>
                                    );
                                })}
                            </tbody>
                        </table>
                    </div>
                </div>

                <Footer />
            </div>
        );
    }
}

export default ViewUsers;