import initialState from './initialState';
import { Actions } from '../actions/actionTypes';

const orderReducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.SET_ORDERS: {
      return {
        ...state, orders:
          action.payload
      };
    }

    case Actions.ADD_ORDER: {
      return {
        ...state, orders: [...state.orders, action.order]
      };
    }

    case Actions.SET_ORDER_NUMBER: {
      return {
        ...state, orderNumber:
          action.payload
      };
    }

    case Actions.SET_COLLECTING_VEHICLE: {
      return {
        ...state, collectingVehicleRegistration:
          action.payload
      };
    }

    case Actions.SET_QUANTITY: {
      return {
        ...state, quantity:
          action.payload
      };
    }

    case Actions.SET_COLLECTION_DATE: {
      return {
        ...state, collectionDate:
          action.payload
      };
    }

    case Actions.SET_DELIVERY_ADDRESS: {
      return {
        ...state, deliveryAddress:
          action.payload
      };
    }

    case Actions.SET_DELIVERY_DATE: {
      return {
        ...state, deliveryDate:
          action.payload
      };
    }

    case Actions.SET_CONTACT_PERSON: {
      return {
        ...state, contactPerson:
          action.payload
      };
    }

    case Actions.SET_COLLECTING_PERSON: {
      return {
        ...state, collectingPerson:
          action.payload
      };
    }

    case Actions.SET_CELLPHONE_NUMBER: {
      return {
        ...state, contactPersonCellNumber:
          action.payload
      };
    }

    case Actions.SET_CONTACT_EMAIL: {
      return {
        ...state, contactPersonEmail:
          action.payload
      };
    }

    case Actions.SET_TOTAL_PRICE: {
      return {
        ...state, totalPrice:
          action.payload
      };
    }

    case Actions.SET_DISPLAY_VALUES: {
      return {
        ...state,
        ...action.payload
      };
    }

    default:
      return state;
  }
};

export default orderReducer;