import React from "react";
import Header from "../header";
import Footer from "../footer";
import "../css/login.css";
import axios from 'axios';
import { Email, Item, renderEmail } from 'react-html-email';
import SimpleReactValidator from 'simple-react-validator';
import { withRouter } from "react-router-dom";
import { AppConfig } from '../GlobalConstants';
import { Helmet } from "react-helmet";
import { Modal, Button } from 'react-bootstrap';

class ForgotPassword extends React.Component {
  constructor() {
    super();

    this.validator = new SimpleReactValidator({
      autoForceUpdate: this,
      className: 'text-danger'
    });
    this.emailAddressChangeHandler = this.emailAddressChangeHandler.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.submitHandler = this.submitHandler.bind(this);

    this.state = {
      emailAddress: '',
      password: '',
      isValidUser: false,
      showModal: false,
      errorMessage: '',
      redirectToLogin: false
    };
  }

  closeModal() {
    this.setState({ showModal: false });
    if (this.state.redirectToLogin === true) {
      this.props.history.push("/login");
    }
  }

  emailAddressChangeHandler(event) {
    this.setState({ emailAddress: event.target.value });
  }

  passwordChangeHandler(event) {
    this.setState({ password: event.target.value });
  }

  submitHandler = (event) => {
    event.preventDefault();

    if (this.validator.allValid()) {
      var emailSent = false;
      axios.post(`${AppConfig.ServerBaseUrl}/users/forgotpassword`, {
        email: this.state.emailAddress
      }).then(response => {
        if (response.data.code !== 200) {
          this.setState({ showModal: true, errorMessage: response.data.message });
        }
        else {
          emailSent = true;
          var emailHtml = renderEmail(
            <Email title='Reset password link'>
              <Item>
                Hello {response.data.username}
              </Item>
              <br />
              <Item>
                Click the link below to reset your JWTGroup application password.
              </Item>
              <br />
              <Item>
                Link: {AppConfig.ClientAppBaseUrl}/reset-password?{response.data.token}
              </Item>
            </Email>
          );

          axios.post(`${AppConfig.ServerBaseUrl}/email`, {
            to: this.state.emailAddress,
            from: 'admin@rjtgroup.co.za',
            subject: "Password Reset Link",
            bodyHtml: emailHtml
          }).then((anotherResponse) => {
            if (anotherResponse.data.code === 200) {
              this.setState({
                showModal: true,
                errorMessage: "Your password reset information has been emailed to you. Check your emails.",
                redirectToLogin: true
              });
            }
            else {
              this.setState({ showModal: true, errorMessage: response.data.message });
            }
          }).catch(() => {
            this.setState({ showModal: true, errorMessage: "Failed to send email address" });
          });
        }
      }).catch(() => {
        this.setState({ showModal: true, errorMessage: "Internal server error occured, please try again" });
      });

      if (emailSent === true) {
        this.props.history.push('/login');
      }
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>Forgot Password - {AppConfig.BasePageTitle}</title>
        </Helmet>

        <Header />

        <div className="container-wrapper">
          <div className="container login-form">
            <form onSubmit={this.submitHandler}>
              <h2 className="text-center">Forgot Password</h2>
              <div className="form-group">
                <input type="text" className="form-control" placeholder="Email address" value={this.state.emailAddress} onChange={this.emailAddressChangeHandler} />
                {this.validator.message('email', this.state.emailAddress, 'required|email')}
              </div>
              <div className="form-group">
                <button type="submit" className="btn btn-primary btn-block">Email Reset Link</button>
              </div>
              <p className="text-center"><a href="/registration">Create an Account</a></p>
            </form>
          </div>
        </div>

        <Footer />

        <Modal show={this.state.showModal} onHide={this.closeModal} animation={false}>
          <Modal.Header closeButton>
            <Modal.Title>Forgot Password</Modal.Title>
          </Modal.Header>
          <Modal.Body>{this.state.errorMessage}</Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={this.closeModal}>
              Ok !!!
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
};

export default withRouter(ForgotPassword);