import React from 'react';
import Header from "../header";
import Footer from "../footer";
import '../css/dashboard.css';
import * as UserAuthentication from '../components/UserAuthentication';
import axios from 'axios';
import { AppConfig } from '../GlobalConstants';
import SimpleReactValidator from 'simple-react-validator';
import { withRouter } from 'react-router-dom';
import { Helmet } from "react-helmet";

class OrderPayment extends React.Component {
    constructor() {
        super();

        this.submitPayment = this.submitPayment.bind(this);
        this.paymentMethodChangeHandler = this.paymentMethodChangeHandler.bind(this);
        this.cardHolderChangeHandler = this.cardHolderChangeHandler.bind(this);
        this.cardNumberChangeHandler = this.cardNumberChangeHandler.bind(this);
        this.expiryDateChangeHandler = this.expiryDateChangeHandler.bind(this);
        this.securityCodeChangeHandler = this.securityCodeChangeHandler.bind(this);

        this.validator = new SimpleReactValidator({
            autoForceUpdate: this,
            className: 'text-danger'
        });

        this.state = {
            paymentMethod: '',
            cardholder: '',
            cardnumber: '',
            expirydate: '',
            securitycode: ''
        };
    }

    componentDidMount() {
        if (!UserAuthentication.AuthenticatedUserId) {
            this.props.history.push('/login');
        }
    }

    // componentDidMount() {
    //     const script = document.createElement("script");
    //     script.src = "https://pi-test.sagepay.com/api/v1/js/sagepay.js";
    //     script.async = true;
    //     document.body.appendChild(script);
    // }

    paymentMethodChangeHandler(event) {
        this.setState({ paymentMethod: event.target.value });
    }

    cardHolderChangeHandler(event) {
        this.setState({ cardholder: event.target.value });
    }

    cardNumberChangeHandler(event) {
        this.setState({ cardnumber: event.target.value });
    }

    expiryDateChangeHandler(event) {
        this.setState({ expirydate: event.target.value });
    }

    securityCodeChangeHandler(event) {
        this.setState({ securitycode: event.target.value });
    }

    saveOrderPaymentMethod() {
        var requestString = this.props.location.search;

        if (requestString === undefined) return "";

        var orderId = requestString.replace("?id=", "");

        axios.post(`${AppConfig.ServerBaseUrl}/user/orders/payment`, {
            "orderId": orderId,
            "paymentMethod": this.state.paymentMethod
        }).then((response) => {
            console.log(response.data.message);
        }).catch();
    };

    submitPayment(event) {
        event.preventDefault();
        if (this.state.paymentMethod !== "online") {
            this.saveOrderPaymentMethod();
        }
        else if (this.validator.allValid()) {
            this.saveOrderPaymentMethod();

            // sagepayOwnForm({ merchantSessionKey: 'FC71C349-3AFE-44CF-915D-7E0A46375B41' })
            //     .tokeniseCardDetails({
            //         cardDetails: {
            //             cardholderName: this.state.cardholder,
            //             cardNumber: this.state.cardnumber,
            //             expiryDate: this.state.expirydate,
            //             securityCode: this.state.securitycode
            //         },
            //         onTokenised: function (result) {
            //             if (result.success) {
            //                 this.setState({cardindentifier: result.cardIdentifier})
            //             } else {
            //                 alert(JSON.stringify(result));
            //             }
            //         }
            //     });
        }
        else {
            this.validator.showMessages();
        }

    }

    render() {
        return (
            <div>
                <Helmet>
                    <title>Order Payment - {AppConfig.BasePageTitle}</title>
                </Helmet>

                <Header />

                <div className="container-wrapper">
                    <div className="container">
                        <div className="card">
                            <h2 class="text-center">Order Payment</h2>
                            <div className="card-body">
                                <form onSubmit={this.submitPayment}>
                                    <div className="form-group">
                                        <label>Payment Method:</label>
                                        <select className="form-control" id="paymentMethod" value={this.state.paymentMethod} onChange={this.paymentMethodChangeHandler}>
                                            <option value="" selected="selected" disabled="true">Select payment method</option>
                                            <option value="online">Online Payment</option>
                                            <option value="eft">EFT</option>
                                            <option value="credit">Credit</option>
                                        </select>
                                    </div>
                                    {
                                        this.state.paymentMethod === "eft" ?
                                            <div className="form-group">
                                                <h4>Use the following details: </h4>
                                                <p>Bank Name: Standard bank</p>
                                                <p>Branch Code: 3352</p>
                                                <p>Account Number: 10118431690</p>
                                                <p>Accouint Type: Current Accout</p>
                                                <p>Account Name: RJT PETROLEUM PRODUCTS</p>
                                                <p className="text-danger">Please make sure to use your order number as refference. You should have your order number on your email.</p>
                                            </div>
                                            :
                                            ''
                                    }
                                    {
                                        this.state.paymentMethod === "credit" ?
                                            <div className="form-group">
                                                <p className="text-danger">
                                                    This option should only be used by regular customers and if its not applicable to you we cannot proceed with your order.
                                                    To find out if you qualify for this method email us at admin@rjtgroup.co.za
                                        </p>
                                            </div>
                                            :
                                            ''
                                    }
                                    {
                                        this.state.paymentMethod === "online" ?
                                            <div>
                                                <h5>Please fill your banking details below</h5>
                                                <div className="form-group">
                                                    <input className="form-control" type="text" placeholder="Cardholder Name" data-card-details="cardholder-name" value={this.state.cardholder} onChange={this.cardHolderChangeHandler} />
                                                    {this.validator.message('card holder', this.state.cardholder, 'required|alpha')}
                                                </div>
                                                <div className="form-group">
                                                    <input className="form-control" type="text" placeholder="Card Number" data-card-details="card-number" value={this.state.cardnumber} onChange={this.cardNumberChangeHandler} />
                                                    {this.validator.message('card number', this.state.cardnumber, 'required|card_num')}
                                                </div>
                                                <div className="form-group">
                                                    <input className="form-control" type="text" placeholder="Expiry Date" data-card-details="expiry-date" value={this.state.expirydate} onChange={this.expiryDateChangeHandler} />
                                                    {this.validator.message('expiry date', this.state.expirydate, 'required|card_exp')}
                                                </div>
                                                <div className="form-group">
                                                    <input className="form-control" type="text" placeholder="Security/Cvc Code" data-card-details="security-code" value={this.state.securitycode} onChange={this.securityCodeChangeHandler} />
                                                    {this.validator.message('security/cvc code', this.state.securitycode, 'required|size:3')}
                                                </div>
                                            </div>
                                            :
                                            ''
                                    }
                                    <button value="Submit" className="btn btn-primary">Submit</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>

                <Footer />
            </div >
        );
    }
}

export default withRouter(OrderPayment);