import React from "react";
import axios from 'axios';
import "../css/contact.scss";
import Header from "../header";
import Footer from "../footer";
import { Email, Item, renderEmail } from 'react-html-email';
import SimpleReactValidator from 'simple-react-validator';
import { AppConfig } from '../GlobalConstants';
import { Helmet } from "react-helmet";
import { Modal, Button } from 'react-bootstrap';

class Contact extends React.Component {
  constructor() {
    super();

    this.validator = new SimpleReactValidator({
      autoForceUpdate: this,
      className: 'text-danger'
    });
    this.nameChangeHandler = this.nameChangeHandler.bind(this);
    this.emailChangeHandler = this.emailChangeHandler.bind(this);
    this.numberChangeHandler = this.numberChangeHandler.bind(this);
    this.subjectChangeHandler = this.subjectChangeHandler.bind(this);
    this.messageChangeHandler = this.messageChangeHandler.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.submitHandler = this.submitHandler.bind(this);

    this.state = {
      name: '',
      email: '',
      number: '',
      subject: '',
      message: '',
      showModal: false
    };
  }

  closeModal() {
    this.setState({ showModal: false });
    window.location.reload();
  }

  nameChangeHandler(event) {
    this.setState({ name: event.target.value });
  }

  emailChangeHandler(event) {
    this.setState({ email: event.target.value });
  }

  numberChangeHandler(event) {
    this.setState({ number: event.target.value });
  }

  subjectChangeHandler(event) {
    this.setState({ subject: event.target.value });
  }

  messageChangeHandler(event) {
    this.setState({ message: event.target.value });
  }



  submitHandler = (event) => {
    event.preventDefault();
    if (this.validator.allValid()) {
      var emailHtml = renderEmail(
        <Email title='link'>
          <Item>
            Hello Admin
          </Item>
          <Item></Item>
          <Item>
            Details:
          </Item>
          <Item>
            Name: {this.state.name}
          </Item>
          <Item>
            Email: {this.state.email}
          </Item>
          <Item>
            Phone Number: {this.state.number}
          </Item>
          <Item>
            Subject: {this.state.subject}
          </Item>
          <Item>
            Message: {this.state.message}
          </Item>
        </Email>
      );

      axios.post(`${AppConfig.ServerBaseUrl}/email`, {
        to: 'sisekoreggieneti@gmail.com',
        from: 'admin@rjtgroup.co.za',
        subject: "Contact Us Form",
        bodyHtml: emailHtml
      }).then(() => {
        this.setState({ showModal: true });
      }).catch(() => {
        console.log("failed to send email");
      });
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>Contact Us - {AppConfig.BasePageTitle}</title>
        </Helmet>

        <Header />

        <div>
          <div className="contact">
            <div className="row">
              <div className="col-3">
                <h2>Contact Us</h2>
              </div>
              <div className="col-9">
                <div className="card">
                  <div className="card-body">
                    <form id="contact-form" onSubmit={this.submitHandler}>
                      <div className="form-group">
                        <input type="text" className="form-control" id="name" placeholder="Full name" value={this.state.name} onChange={this.nameChangeHandler} />
                        {this.validator.message('full name', this.state.name, 'required|string')}
                      </div>
                      <div className="form-group">
                        <input type="email" className="form-control" id="email" placeholder="Email address" value={this.state.email} onChange={this.emailChangeHandler} />
                        {this.validator.message('email address', this.state.email, 'required|email')}
                      </div>
                      <div className="form-group">
                        <input type="text" className="form-control" id="number" placeholder="Phone number" value={this.state.number} onChange={this.numberChangeHandler} />
                        {this.validator.message('phone number', this.state.number, 'phone')}
                      </div>
                      <div className="form-group">
                        <input type="subject" className="form-control" id="subject" placeholder="Subject" value={this.state.subject} onChange={this.subjectChangeHandler} />
                      </div>
                      <div className="form-group">
                        <label>Message</label>
                        <textarea className="form-control" id="message" rows="6" value={this.state.message} onChange={this.messageChangeHandler}></textarea>
                        {this.validator.message('message', this.state.message, 'required|string')}
                      </div>
                      <div className="mx-auto">
                        <button type="submit" className="btn text-right">Submit</button></div>
                    </form>
                  </div>
                </div>
              </div>
            </div>

            <div className="row row-margin">
              <div className="col-3">
                <h2>Get In Touch</h2>
              </div>
              <div className="col-9">
                <div className="card">
                  <div className="card-body text-center">
                    <p>35851 Cephe Crescent, Harare, Khayelitsha, 7784</p>
                    <p>Tel: <a href="tel:+27137733001">+27 137 733 001</a> / <a href="tel:+27768296398">+27 768 296 398</a></p>
                    <p><a href="mailto:admin@rjtgroup.co.za">admin@rjtgroup.co.za</a> / <a href="mailto:mpilo@rjtgroup.co.za">mpilo@rjtgroup.co.za</a></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <Footer />

        <Modal show={this.state.showModal} onHide={this.closeModal} animation={false}>
          <Modal.Header closeButton>
            <Modal.Title>Contact Form</Modal.Title>
          </Modal.Header>
          <Modal.Body>Your message has been submitted and one of our team members will get to you shortly.</Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={this.closeModal}>
              Thank You!!!
                        </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default Contact;