import axios from 'axios';
import { Actions } from './actionTypes';
import { AppConfig } from '../GlobalConstants';

export function getAllOrders() {
  return (dispatch) => {
    return axios.post(`${AppConfig.ServerBaseUrl}/user/allorders`)
      .then((response) => {
        dispatch(setOrders(response.data.orders));
      });
  };
}

export const setOrders = (orders) => ({
  type: Actions.SET_ORDERS,
  payload: orders
});

export function getUserOrders(customerId) {
  return (dispatch) => {
    return axios.post(`${AppConfig.ServerBaseUrl}/user/orders`, {
      "userId": customerId
    }).then((response) => {
      dispatch(setOrders(response.data.orders));
    }).catch();
  };
}