import React from 'react';
import Header from "../header";
import Footer from "../footer";
import axios from 'axios';
import SimpleReactValidator from 'simple-react-validator';
import { AppConfig } from '../GlobalConstants';
import { Email, Item, renderEmail } from 'react-html-email';
import { Modal, Button } from 'react-bootstrap';
import { Helmet } from "react-helmet";
import { connect } from 'react-redux';

class QuoteDetails extends React.Component {
    constructor() {
        super();

        this.sendEmail = this.sendEmail.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.emailChangeHandler = this.emailChangeHandler.bind(this);

        this.validator = new SimpleReactValidator({
            autoForceUpdate: this,
            className: 'text-danger'
        });

        this.state = {
            distancePrice: 0.0,
            dieselPrice: 0.0,
            price: 0.0,
            ordermethod: '',
            email: '',
            showModal: false,
            errorMessage: ''
        };
    }

    closeModal() {
        this.setState({ showModal: false });
        this.props.history.push("/order");
    }

    emailChangeHandler(event) {
        this.setState({ email: event.target.value });
    }

    componentDidMount() {
        if (this.props.quoteDistance !== '') {
            var dieselPrice = parseFloat(this.props.quoteFinalDieselPrice);
            var distancePrice = parseFloat(this.props.quoteDistance) * AppConfig.DistancePrice;

            this.setState({
                ordermethod: "Delivery",
                distancePrice: distancePrice,
                dieselPrice: dieselPrice,
                price: dieselPrice + distancePrice
            });
        }
        else {
            this.setState({
                ordermethod: "On Site",
                price: this.props.quoteFinalDieselPrice
            });
        }

    }

    saveQuoteDetails() {
        axios.post(`${AppConfig.ServerBaseUrl}/quote`, {
            email: `${this.state.email}`,
            ordermethod: this.state.ordermethod,
            price: this.state.price
        }).then().catch(() => {
            this.setState({ showModal: true, errorMessage: "An internal error has occured. Please try again later." });
        });
    }

    sendEmail(event) {
        event.preventDefault();
        var emailHtml = renderEmail(
            <Email title='Reset password link'>
                <Item>
                    Dear User
              </Item>
                <br />
                <Item>
                    You have requested a diesel quote from the RJT Group application. Here are your quote details below:
              </Item>
                <br />
                <Item>
                    Quote Method: {this.state.ordermethod}
                </Item>
                <Item>
                    Total Price: R{this.state.price} {this.state.distancePrice !== 0.0 ? '(including transportation)' : ''}
                </Item>
                <br />
                <Item>
                    Best Regards
                </Item>

                <Item>
                    RJT Group
                </Item>

            </Email>
        );

        if (this.validator.allValid()) {
            axios.post(`${AppConfig.ServerBaseUrl}/email`, {
                to: `${this.state.email}`,
                from: 'admin@rjtgroup.co.za',
                subject: "RJT Group Diesel Quotation Invoice",
                bodyHtml: emailHtml
            }).then((response) => {
                if (response.data.code === 200) {
                    this.setState({ showModal: true, errorMessage: "Email has been sent" });
                    this.saveQuoteDetails();
                }
                else {
                    this.setState({ showModal: true, errorMessage: response.data.message });
                }
            }).catch(() => {
                this.setState({ showModal: true, errorMessage: "An internal error has occured. Please try again later." });
            });
        }
        else {
            this.validator.showMessages();
        }
    }

    render() {
        return (
            <div>
                <Helmet>
                    <title>Quote details - {AppConfig.BasePageTitle}</title>
                </Helmet>

                <Header />

                <div className="container-wrapper">
                    <div className="container">
                        <div className="card">
                            <h2 className="text-center">Quote Details</h2>
                            <div className="card-body">
                                <div className="">
                                    <p>Quote Method: {this.state.ordermethod}</p>
                                    <p>Total Price: R{this.state.price} {this.state.ordermethod === 'Delivery' ? '(Including transportation)' : ''}</p>
                                </div>
                                <div className="form-group">
                                    <h6>To email quote fill this field</h6>
                                    <input className="form-control" type="email" placeholder="Email address" value={this.state.email} onChange={this.emailChangeHandler} />
                                    {this.validator.message('email', this.state.email, 'required|email')}
                                </div>
                                <div className="labelWithButtons">
                                    <div>
                                        <a className="btn btn-primary text-right quote-details-button" href="/order">Order Diesel</a>
                                    </div>
                                    <div>
                                        <a className="btn btn-primary text-right" href="#" onClick={this.sendEmail}>Email Quote</a>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>

                <Footer />

                <Modal show={this.state.showModal} onHide={this.closeModal} animation={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>Forgot Password</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>{this.state.errorMessage}</Modal.Body>
                    <Modal.Footer>
                        <Button variant="primary" onClick={this.closeModal}>
                            Ok !!!
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        quoteDistance: state.quoteReducer.quoteDistance,
        quoteFinalDieselPrice: state.quoteReducer.quoteFinalDieselPrice
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        dispatch
    };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(QuoteDetails);