import React from 'react';
import Header from "../header";
import Footer from "../footer";
import '../css/dashboard.css';
import * as UserAuthentication from '../components/UserAuthentication';
import { AppConfig } from '../GlobalConstants';
import { withRouter } from 'react-router-dom';
import { Helmet } from "react-helmet";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as orderActions from '../actions/orderActions';
import PropTypes from 'prop-types';

class Dashboard extends React.Component {
    constructor() {
        super();

        this.state = {
            orderIdForAction: null
        };
    }

    componentDidMount() {
        var customerId = UserAuthentication.AuthenticatedUserId;

        if (!customerId) {
            this.props.history.push('/login');
        }

        this.props.orderActions.getUserOrders(customerId);
    }

    render() {
        return (
            <div>
                <Helmet>
                    <title>User Dashboard - {AppConfig.BasePageTitle}</title>
                </Helmet>

                <Header />

                <div className="container-wrapper">
                    <div className="container dashboard">
                        {
                            this.props.orders === undefined ?
                                <h2>You do not have any orders</h2> :
                                <h2>Here is a list of your past orders</h2>
                        }
                        <div className="table-responsive">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th>#Order Number</th>
                                        <th>Order Method</th>
                                        <th>Creation Date</th>
                                        <th>Collection/Delivery Date</th>
                                        <th>Quantity (litres)</th>
                                        <th>Total Price</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.props.orders !== undefined && this.props.orders.map((item) => {
                                        var orderDate = item.OrderMethod === "delivery" ?
                                            Date.parse(item.DeliveryDate) :
                                            Date.parse(item.CollectionDate);
                                        var currentDate = Date.now();
                                        var dateList = item.CreationDate.replace('Z', '').split(/[-T:]/);
                                        var creationDate = new Date(Date.UTC(dateList[0], dateList[1] - 1, dateList[2], dateList[3], dateList[4], dateList[5]));
                                        return <tr>
                                            <td>{item.OrderNumber}</td>
                                            <td>{item.OrderMethod === "delivery" ? "Delivery" : "Collection"}</td>
                                            <td>{new Intl.DateTimeFormat("en-GB", {
                                                year: "numeric",
                                                month: "long",
                                                day: "2-digit"
                                            }).format(creationDate)}</td>
                                            <td>{new Intl.DateTimeFormat("en-GB", {
                                                year: "numeric",
                                                month: "long",
                                                day: "2-digit"
                                            }).format(orderDate)}</td>
                                            <td>{item.Quantity}</td>
                                            <td>R{item.TotalPrice}</td>
                                        </tr>;
                                    })}

                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                <Footer />
            </div >
        );
    }
}

Dashboard.propTypes = {
    orderActions: PropTypes.object,
    orders: PropTypes.array
};

const mapStateToProps = (state) => {
    return {
        orders: state.orderReducer.orders
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        orderActions: bindActionCreators(orderActions, dispatch)
    };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(withRouter(Dashboard));