import initialState from './initialState';
import { Actions } from '../actions/actionTypes';

const quoteReducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.SET_QUOTE_QUANTITY: {
      return {
        ...state, quoteQuantity:
          action.payload
      };
    }

    case Actions.SET_QUOTE_ADDRESS: {
      return {
        ...state, quoteAddress:
          action.payload
      };
    }

    case Actions.SET_QUOTE_DISTANCE: {
      return {
        ...state, quoteDistance:
          action.payload
      };
    }

    case Actions.SET_QUOTE_SHOW_ADDRESS: {
      return {
        ...state, quoteShowAddress:
          action.payload
      };
    }

    case Actions.SET_QUOTE_FINAL_DIESEL_PRICE: {
      return {
        ...state, quoteFinalDieselPrice:
          action.payload
      };
    }

    default:
      return state;
  }
};

export default quoteReducer;