import React from "react";
import Header from "../header";
import Footer from "../footer";
import "../css/login.css";
import axios from 'axios';
import SimpleReactValidator from 'simple-react-validator';
import { AppConfig } from '../GlobalConstants';
import { Helmet } from "react-helmet";
import { Modal, Button } from 'react-bootstrap';

class ResetPassword extends React.Component {
    constructor() {
        super();

        this.validator = new SimpleReactValidator({
            autoForceUpdate: this,
            className: 'text-danger',
            validators: {
                matchPassword: {
                    message: 'The :attribute must match the password field.',
                    rule: (val, params, validator) => {
                        return val == this.state.password;
                    },
                    messageReplace: (message, params) => message.replace(':values', this.helpers.toSentence(params)),  // optional
                    required: true  // optional
                }
            }
        });
        this.passwordChangeHandler = this.passwordChangeHandler.bind(this);
        this.confirmPasswordChangeHandler = this.confirmPasswordChangeHandler.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.submitHandler = this.submitHandler.bind(this);

        this.state = {
            username: '',
            confirmPassword: '',
            password: '',
            isValidUser: false,
            users: [],
            showModal: false,
            errorMessage: '',
            redirectToLogin: false
        };
    }

    closeModal() {
        this.setState({ showModal: false });
        if (this.state.redirectToLogin === true) {
            this.props.history.push("/login");
        }
    }

    confirmPasswordChangeHandler(event) {
        this.setState({
            confirmPassword: event.target.value
        });
    }

    passwordChangeHandler(event) {
        this.setState({
            password: event.target.value
        });
    }

    submitHandler = (event) => {
        event.preventDefault();
        if (this.validator.allValid()) {
            var encrypt = require('md5');
            var encryptedPassword = encrypt(this.state.password);
            var token = this.props.location.search;

            axios.post(`${AppConfig.ServerBaseUrl}/users/updatepassword`, {
                token: token.substring(1),
                password: encryptedPassword
            }).then((response) => {
                if (response.data.code === 200) {
                    this.setState({ showModal: true, errorMessage: response.data.message, redirectToLogin: true });
                }
                else {
                    this.setState({ showModal: true, errorMessage: response.data.message });
                }
            }).catch(() => {
                this.setState({ showModal: true, errorMessage: "An internal error occured, please try again" });
            });

        } else {
            this.validator.showMessages();
        }
    };

    render() {
        return (
            <div>
                <Helmet>
                    <title>Reset Password - {AppConfig.BasePageTitle}</title>
                </Helmet>

                <Header />

                <div className="container-wrapper">
                    <div className="container login-form">
                        <form onSubmit={this.submitHandler}>
                            <h2 className="text-center">Reset Password</h2>
                            <div className="form-group">
                                <input type="password" className="form-control" placeholder="New Password" value={this.state.password} onChange={this.passwordChangeHandler} />
                                {this.validator.message('new password', this.state.password, 'required')}
                            </div>
                            <div className="form-group">
                                <input type="password" className="form-control" placeholder="Confirm Password" value={this.state.confirmPassword} onChange={this.confirmPasswordChangeHandler} />
                                {this.validator.message('confirm password', this.state.confirmPassword, 'required|matchPassword')}
                            </div>
                            <div className="form-group">
                                <label className="pull-left checkbox-inline"><input type="checkbox" /> Remember me</label>
                            </div>
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary btn-block">Reset Password</button>
                            </div>
                        </form>
                    </div>
                </div>

                <Footer />

                <Modal show={this.state.showModal} onHide={this.closeModal} animation={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>Reset Password</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>{this.state.errorMessage}</Modal.Body>
                    <Modal.Footer>
                        <Button variant="primary" onClick={this.closeModal}>
                            Ok !!!
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>

        );
    }
};

export default ResetPassword;