import React from 'react';
import "./css/header.scss";
import axios from 'axios';
import logo from "./images/Logo.png";
import Nav from '../node_modules/react-bootstrap/Nav';
import Navbar from '../node_modules/react-bootstrap/Navbar';
import * as UserAuthentication from './components/UserAuthentication';
import { AppConfig } from './GlobalConstants';

class Header extends React.Component {
  constructor() {
    super();

    this.state = {
      isAdmin: false
    };
  }

  async componentWillMount() {
    const response = await axios.post(`${AppConfig.ServerBaseUrl}/users/user`, {
      "userId": UserAuthentication.AuthenticatedUserId
    });
    this.setState({ isAdmin: response.data.admin });
  }

  render() {
    const isUserAuthenticated = UserAuthentication.IsUserAuthenticated;
    const userAuthentication = isUserAuthenticated ?
      { path: "/", name: "Logout" } :
      { path: "/login", name: "Login" };

    const dashboardButton = isUserAuthenticated ?
      <Nav.Link href="/dashboard">Dashboard</Nav.Link> : '';

    const orderDiesel = isUserAuthenticated ?
      <Nav.Link href="/order">Order Diesel</Nav.Link> : '';

    const manageUsers = isUserAuthenticated && this.state.isAdmin ?
      <Nav.Link href="/manage-users">Manage Users</Nav.Link> : '';

    return (
      <div>
        <Navbar className="page-header" collapseOnSelect expand="lg" variant="dark">
          <Navbar.Brand href="/"><img className="logo-image" alt="Logo Image" src={logo} /></Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto"></Nav>
            <Nav>
              <Nav.Link href="/contact">Contact Us</Nav.Link>
              {orderDiesel}
              {dashboardButton}
              {manageUsers}
              <Nav.Link onSelect={UserAuthentication.HandleLogout} href={userAuthentication.path} id="login-logout">{userAuthentication.name}</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <div className="marigold-space"></div>
      </div>

    );
  }
}

export default Header;