import React from 'react';
import axios from 'axios';
import Header from '../header';
import Footer from '../footer';
import * as UserAuthentication from '../components/UserAuthentication';
import { AppConfig } from '../GlobalConstants';
import { Modal, Button } from 'react-bootstrap';
import { Helmet } from "react-helmet";

class ManageUsers extends React.Component {
  constructor() {
    super();

    this.makeNonAdmin = this.makeNonAdmin.bind(this);
    this.makeAdmin = this.makeAdmin.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.continueWithUpdateAction = this.continueWithUpdateAction.bind(this);

    this.state = {
      users: [],
      userId: null,
      showModal: false,
      modalBody: '',
      modalTitle: '',
      modalAction: null
    };
  }

  async componentDidMount() {
    const response = await axios.post(`${AppConfig.ServerBaseUrl}/users/user`, {
      "userId": UserAuthentication.AuthenticatedUserId
    });

    if (!response.data.admin) {
      this.props.history.push('/');
    }

    axios.get(`${AppConfig.ServerBaseUrl}/users`).then((response) => {
      this.setState({ users: response.data.users });
    }).catch();
  }

  handleClose() {
    this.setState({ showModal: false });
  }

  makeNonAdmin(event) {
    this.setState({
      modalAction: 'non-admin',
      modalTitle: "Update Admin Rights",
      modalBody: "Are you 100% sure that you want to remove admin rights for this user?",
      showModal: true,
      userId: event.currentTarget.dataset.id
    });
  }

  makeAdmin(event) {
    this.setState({
      modalAction: 'make-admin',
      modalTitle: "Update Admin Rights",
      modalBody: "Are you 100% sure that you want to give admin rights to this user?",
      showModal: true,
      userId: event.currentTarget.dataset.id
    });
  }

  continueWithUpdateAction() {
    this.handleClose();
    if (this.state.modalAction === 'non-admin') {
      axios.post(`${AppConfig.ServerBaseUrl}/user/admin`, {
        userId: this.state.userId,
        isAdmin: 0
      }).then((response) => {
        if (response.data.status === 200) {
          this.setState({ modalAction: null });
        }
        window.location.reload();
      }).catch();
    }
    else if (this.state.modalAction === 'make-admin') {
      axios.post(`${AppConfig.ServerBaseUrl}/user/admin`, {
        userId: this.state.userId,
        isAdmin: 1
      }).then((response) => {
        if (response.data.status === 200) {
          this.setState({ modalAction: null });
        }
        window.location.reload();
      }).catch();
    }
  }

  render() {
    return (
      <div>
        <Helmet>
          <title>Manage Users - {AppConfig.BasePageTitle}</title>
        </Helmet>

        <Header />

        <div className="container-wrapper">

          <div className="container dashboard">
            <h2>Site Users</h2>
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th>#User Id</th>
                    <th>Organisation Name</th>
                    <th>Telephone</th>
                    <th>Email Address</th>
                    <th>Creation Date</th>
                    <th>Contact Person Name</th>
                    <th>Contact Person Cellphone</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {this.state.users !== undefined && this.state.users.map(item => {
                    return (
                      <tr>
                        <th scope="row">{item.Id}</th>
                        <td>{item.CompanyName}</td>
                        <td>{item.CompanyTelephoneNumber}</td>
                        <td>{item.EmailAddress}</td>
                        <td>{item.CreatedDate}</td>
                        <td>{item.ContactPersonName}</td>
                        <td>{item.ContactPersonCellphone}</td>
                        <td>{item.IsAdmin ?
                          <a id="remove-admin" data-id={item.Id} href="#" className="btn btn-primary" onClick={this.makeNonAdmin.bind(this)}>Make Non-Admin</a> :
                          <a id="make-admin" data-id={item.Id} href="#" className="btn btn-primary" onClick={this.makeAdmin.bind(this)}>Make Admin</a>}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <Footer />

        <Modal show={this.state.showModal} onHide={this.handleClose} animation={false}>
          <Modal.Header closeButton>
            <Modal.Title>{this.state.modalTitle}</Modal.Title>
          </Modal.Header>
          <Modal.Body>{this.state.modalBody}</Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={this.continueWithUpdateAction}>
              Yes
                        </Button>
            <Button variant="primary" onClick={this.handleClose}>
              No
                        </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default ManageUsers;