import React from 'react';
import Header from "../header";
import Footer from "../footer";
import '../css/dashboard.css';
import * as UserAuthentication from '../components/UserAuthentication';
import axios from 'axios';
import { AppConfig } from '../GlobalConstants';
import { Modal, Button } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';
import { Helmet } from "react-helmet";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as orderActions from '../actions/orderActions';
import PropTypes from 'prop-types';

class Dashboard extends React.Component {
    constructor() {
        super();

        this.approveQuote = this.approveQuote.bind(this);
        this.declineQuote = this.declineQuote.bind(this);
        this.editQuote = this.editQuote.bind(this);
        this.deleteQuote = this.deleteQuote.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.continueWithOrderAction = this.continueWithOrderAction.bind(this);
        this.continueWithOrderDeletion = this.continueWithOrderDeletion.bind(this);
        this.handleAlertClose = this.handleAlertClose.bind(this);

        this.state = {
            showModal: false,
            showAlert: false,
            orderIdForAction: null,
            modalBody: '',
            modalTitle: '',
            modalAction: ''
        };
    }

    approveQuote(event) {
        this.setState({
            modalAction: 'approve',
            modalTitle: "Approve Order",
            modalBody: "Are you 100% sure that you want to approve the order?",
            showModal: true,
            orderIdForAction: event.currentTarget.dataset.id
        });
    }

    declineQuote(event) {
        this.setState({
            modalAction: 'decline',
            modalTitle: "Decline Order",
            modalBody: "Are you 100% sure that you want to decline the order?",
            showModal: true,
            orderIdForAction: event.currentTarget.dataset.id
        });
    }

    editQuote(event) {
        //console.log(`Edit Quote: ${event.currentTarget.dataset.id}`);
    }

    deleteQuote(event) {
        this.setState({
            modalAction: 'delete',
            modalTitle: "Delete Order",
            modalBody: "Are you 100% sure that you want to delete the order?",
            showModal: true,
            orderIdForAction: event.currentTarget.dataset.id
        });
    }

    continueWithOrderAction() {
        this.handleClose();
        switch (this.state.modalAction) {
            case "delete":
                this.continueWithOrderDeletion();
            case "approve":
                this.updateOrderStatus("approved");
            case "decline":
                this.updateOrderStatus("declined");
        }
    }

    continueWithOrderDeletion() {
        axios.post(`${AppConfig.ServerBaseUrl}/order/delete`, {
            orderId: this.state.orderIdForAction
        }).then(() => {
            window.location.reload();
        }).catch();
    }

    updateOrderStatus(status) {
        axios.post(`${AppConfig.ServerBaseUrl}/order/status`, {
            orderId: this.state.orderIdForAction,
            status
        }).then(() => {
            window.location.reload();
        }).catch();
    }

    handleAlertClose() {
        this.setState({ showAlert: false });
    }

    handleClose() {
        this.setState({ showModal: false });
    }

    componentDidMount() {
        var customerId = UserAuthentication.AuthenticatedUserId;

        if (!customerId) {
            this.props.history.push('/login');
        }

        this.props.orderActions.getAllOrders();
    }

    render() {
        return (
            <div>
                <Helmet>
                    <title>Admin Dashboard - {AppConfig.BasePageTitle}</title>
                </Helmet>

                <Header />

                <div className="container-wrapper">
                    <div className="container dashboard">
                        <h2>Admin Dashboard</h2>
                        <div className="table-responsive">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th>#Order Number</th>
                                        <th>Organisation Name</th>
                                        <th>Order Method</th>
                                        <th>Creation Date</th>
                                        <th>Collection/Delivery Date</th>
                                        <th>Quantity (litres)</th>
                                        <th>Total Price</th>
                                        <th>Payment</th>
                                        <th>Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.props.orders !== undefined && this.props.orders.map((item, index) => {
                                        var orderDate = item.OrderMethod === "delivery" ?
                                            Date.parse(item.DeliveryDate) :
                                            Date.parse(item.CollectionDate);
                                        var currentDate = Date.now();
                                        var dateList = item.CreationDate.replace('Z', '').split(/[-T:]/);
                                        var creationDate = new Date(Date.UTC(dateList[0], dateList[1] - 1, dateList[2], dateList[3], dateList[4], dateList[5]));
                                        return <tr key={index}>
                                            <td>{item.OrderNumber}</td>
                                            <td>{item.CompanyName}</td>
                                            <td>{item.OrderMethod}</td>
                                            <td>{new Intl.DateTimeFormat("en-GB", {
                                                year: "numeric",
                                                month: "long",
                                                day: "2-digit"
                                            }).format(creationDate)}</td>
                                            <td>{new Intl.DateTimeFormat("en-GB", {
                                                year: "numeric",
                                                month: "long",
                                                day: "2-digit"
                                            }).format(orderDate)}</td>
                                            <td>{item.Quantity}</td>
                                            <td>R{item.TotalPrice}</td>
                                            <td>{item.PaymentReceived ? `Paid via ${item.PaymentMethod}` : 'Pending'}</td>
                                            {orderDate > currentDate ?
                                                (
                                                    <td>
                                                        {
                                                            (item.PaymentMethod === 'credit' && item.Status !== 'approved' && item.Status !== 'declined') ?
                                                                <div>
                                                                    <a id="approve-order" data-id={item.Id} href="#" className="btn btn-primary" onClick={this.approveQuote.bind(this)}>Approve</a>
                                                                    <a id="decline-order" data-id={item.Id} href="#" className="btn btn-primary" onClick={this.declineQuote.bind(this)}>Decline</a>
                                                                </div> :
                                                                <div>
                                                                    <p>Active</p>
                                                                </div>
                                                        }
                                                    </td>
                                                ) :
                                                <td>Processed</td>}

                                        </tr>;
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                <Footer />

                <Modal show={this.state.showModal} onHide={this.handleClose} animation={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>{this.state.modalTitle}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>{this.state.modalBody}</Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.continueWithOrderAction}>
                            Yes
                        </Button>
                        <Button variant="primary" onClick={this.handleClose}>
                            No
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div >
        );
    }
}

Dashboard.propTypes = {
    orderActions: PropTypes.object,
    orders: PropTypes.array
};

const mapStateToProps = (state) => {
    return {
        orders: state.orderReducer.orders
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        orderActions: bindActionCreators(orderActions, dispatch)
    };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(withRouter(Dashboard));