import cookie from 'react-cookies';
import axios from 'axios';
import { AppConfig } from '../GlobalConstants';

const sessionCookie = cookie.load('session_token');
export const IsUserAuthenticated = sessionCookie !== undefined;
export const AuthenticatedUserId = IsUserAuthenticated ? cookie.load("user_id") : '';

export const IsAdmin = () => {
  if (IsUserAuthenticated && AuthenticatedUserId !== '') {
    axios.post(`${AppConfig.ServerBaseUrl}/users/user`, {
      "userId": AuthenticatedUserId
    }).then((response) => {
      return response.data.admin === 1;
    }).catch(() => {
      return false;
    });
  }
  return false;
};

export const HandleLogout = () => {
  if (IsUserAuthenticated) {
    cookie.remove('session_token', { path: '/' });
    cookie.remove('user_session', { path: '/' });
    cookie.remove('user_id', { path: '/' });
  }
};

