import React from 'react';
import axios from 'axios';
import Dashboard from '../pages/dashboard';
import AdminDashboard from '../pages/admin-dashboard';
import { AppConfig } from '../GlobalConstants';
import * as UserAuthentication from '../components/UserAuthentication';

class ShowLoogedInUserDashboard extends React.Component {
    constructor() {
        super();

        this.state = {
            isAdmin: false
        };
    }

    async componentWillMount() {
        const response = await axios.post(`${AppConfig.ServerBaseUrl}/users/user`, {
            "userId": UserAuthentication.AuthenticatedUserId
        });
        this.setState({ isAdmin: response.data.admin });
    }

    render() {
        return (
            <div>
                {
                    this.state.isAdmin ? <AdminDashboard /> : <Dashboard />
                }
            </div>
        );
    }
}

export default ShowLoogedInUserDashboard;