import React from "react";
import axios from 'axios';
import "../css/contact.scss";
import Header from "../header";
import Footer from "../footer";
import SimpleReactValidator from 'simple-react-validator';
import { AppConfig } from '../GlobalConstants';
import { Helmet } from "react-helmet";

class Contact extends React.Component {
  constructor() {
    super();

    this.validator = new SimpleReactValidator({
      autoForceUpdate: this,
      className: 'text-danger',
      validators: {
        matchPassword: {
          message: 'The :attribute must match the password field.',
          rule: (val, params, validator) => {
            return val == this.state.password;
          },
          messageReplace: (message, params) => message.replace(':values', this.helpers.toSentence(params)),  // optional
          required: true  // optional
        }
      }
    });
    this.organisationNameChangeHandler = this.organisationNameChangeHandler.bind(this);
    this.emailAddressChangeHandler = this.emailAddressChangeHandler.bind(this);
    this.telephoneChangeHandler = this.telephoneChangeHandler.bind(this);
    this.addressChangeHandler = this.addressChangeHandler.bind(this);
    this.personNameChangeHandler = this.personNameChangeHandler.bind(this);
    this.cellphoneNumberChangeHandler = this.cellphoneNumberChangeHandler.bind(this);
    this.passwordChangeHandler = this.passwordChangeHandler.bind(this);
    this.confirmPasswordChangeHandler = this.confirmPasswordChangeHandler.bind(this);
    this.submitHandler = this.submitHandler.bind(this);

    this.state = {
      organisationName: '',
      emailAddress: '',
      telephone: '',
      address: '',
      personName: '',
      cellphoneNumber: '',
      password: '',
      confirmPassword: ''
    };
  }

  organisationNameChangeHandler(event) {
    this.setState({ organisationName: event.target.value });
  }

  emailAddressChangeHandler(event) {
    this.setState({ emailAddress: event.target.value });
  }

  telephoneChangeHandler(event) {
    this.setState({ telephone: event.target.value });
  }

  addressChangeHandler(event) {
    this.setState({ address: event.target.value });
  }

  personNameChangeHandler(event) {
    this.setState({ personName: event.target.value });
  }

  cellphoneNumberChangeHandler(event) {
    this.setState({ cellphoneNumber: event.target.value });
  }

  passwordChangeHandler(event) {
    this.setState({ password: event.target.value });
  }

  confirmPasswordChangeHandler(event) {
    this.setState({ confirmPassword: event.target.value });
  }

  submitHandler = (event) => {
    if (this.validator.allValid()) {

      var encrypt = require('md5');

      axios.post(`${AppConfig.ServerBaseUrl}/customers`, {
        organisationName: this.state.organisationName,
        emailAddress: this.state.emailAddress,
        telephone: this.state.telephone,
        address: this.state.address,
        personName: this.state.personName,
        cellphoneNumber: this.state.cellphoneNumber,
        password: encrypt(this.state.password)
      }).then(() => {
        this.props.history.push("/login");
      }).catch(() => {
        console.log("failed to send email");
      });
      this.props.history.push("/login");
    } else {
      event.preventDefault();
      this.validator.showMessages();
    }

  };

  render() {
    return (
      <div>
        <Helmet>
          <title>Registration - {AppConfig.BasePageTitle}</title>
        </Helmet>

        <Header />

        <div className="container-wrapper">
          <div className="container">
            <div className="card">
              <h2 className="text-center">Registration</h2>
              <div className="card-body">
                <form onSubmit={this.submitHandler}>
                  <div className="form-group">
                    <input type="text" className="form-control" id="name" placeholder="Organisation name" value={this.state.organisationName} onChange={this.organisationNameChangeHandler} />
                    {this.validator.message('organisation name', this.state.organisationName, 'required|string')}
                  </div>
                  <div className="form-group">
                    <input type="email" className="form-control" id="email" placeholder="Email address" value={this.state.emailAddress} onChange={this.emailAddressChangeHandler} />
                    {this.validator.message('email address', this.state.emailAddress, 'required|email')}
                  </div>
                  <div className="form-group">
                    <input type="text" className="form-control" id="number" placeholder="Telehone number" value={this.state.telephone} onChange={this.telephoneChangeHandler} />
                    {this.validator.message('telephone number', this.state.telephone, 'required|phone')}
                  </div>
                  <div className="form-group">
                    <input type="text" className="form-control" id="address" placeholder="Physical address" value={this.state.address} onChange={this.addressChangeHandler} />
                    {this.validator.message('physical address', this.state.address, 'required|string')}
                  </div>
                  <div className="form-group">
                    <input type="text" className="form-control" id="personName" placeholder="Full contact person name" value={this.state.personName} onChange={this.personNameChangeHandler} />
                    {this.validator.message('contact person name', this.state.personName, 'required|string')}
                  </div>
                  <div className="form-group">
                    <input type="text" className="form-control" id="personNumber" placeholder="Contact person cellphone number" value={this.state.cellphoneNumber} onChange={this.cellphoneNumberChangeHandler} />
                    {this.validator.message('contact person number', this.state.cellphoneNumber, 'required|phone')}
                  </div>
                  <div className="form-group">
                    <input type="password" className="form-control" id="password" placeholder="Password" value={this.state.password} onChange={this.passwordChangeHandler} />
                    {this.validator.message('password', this.state.password, 'required|min:5')}
                  </div>
                  <div className="form-group">
                    <input type="password" className="form-control" id="confirmpassword" placeholder="Confirm Password" value={this.state.confirmPassword} onChange={this.confirmPasswordChangeHandler} />
                    {this.validator.message('confirm password', this.state.confirmPassword, 'required|min:5|matchPassword')}
                  </div>
                  <div className="mx-auto">
                    <button type="submit" className="btn btn-primary text-right">Submit</button></div>
                </form>
              </div>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    );
  }
}

export default Contact;