import React from "react";
import Header from "../header";
import Footer from "../footer";
import "../css/login.css";
import SimpleReactValidator from 'simple-react-validator';
import axios from 'axios';
import cookie from 'react-cookies';
import { AppConfig } from '../GlobalConstants';
import { Helmet } from "react-helmet";
import { Modal, Button } from 'react-bootstrap';

class Login extends React.Component {
  constructor() {
    super();

    this.validator = new SimpleReactValidator({
      autoForceUpdate: this,
      className: 'text-danger'
    });
    this.emailAddressChangeHandler = this.emailAddressChangeHandler.bind(this);
    this.passwordChangeHandler = this.passwordChangeHandler.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.submitHandler = this.submitHandler.bind(this);

    this.state = {
      emailAddress: '',
      password: '',
      isValidUser: false,
      showModal: false,
      errorMessage: ''
    };
  }

  closeModal() {
    this.setState({ showModal: false });
  }

  emailAddressChangeHandler(event) {
    this.setState({ emailAddress: event.target.value });
  }

  passwordChangeHandler(event) {
    this.setState({ password: event.target.value });
  }

  submitHandler = (event) => {
    event.preventDefault();
    if (this.validator.allValid()) {
      var encrypt = require('md5');
      var encryptedPassword = encrypt(this.state.password);

      axios.post(`${AppConfig.ServerBaseUrl}/users/login`, {
        email: this.state.emailAddress,
        password: encryptedPassword
      }).then(response => {
        var statusCode = response.data.code;
        if (statusCode === 200) {
          cookie.save('session_token', response.data.token, { path: '/' });
          cookie.save('user_session', response.data.user, { path: '/' });
          cookie.save('user_id', response.data.id, { path: '/' });
          window.location.href = `${AppConfig.ClientAppBaseUrl}/order`;
        }
        else {
          this.setState({ showModal: true, errorMessage: response.data.message });
        }
      }).catch();
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>Login - {AppConfig.BasePageTitle}</title>
        </Helmet>

        <Header />

        <div className="container-wrapper">
          <div className="container login-form">
            <form onSubmit={this.submitHandler}>
              <h2 className="text-center">Log in</h2>
              <div className="form-group">
                <input type="text" className="form-control" placeholder="Email address" value={this.state.emailAddress} onChange={this.emailAddressChangeHandler} />
                {this.validator.message('email', this.state.emailAddress, 'required|email')}
              </div>
              <div className="form-group">
                <input type="password" className="form-control" placeholder="Password" value={this.state.password} onChange={this.passwordChangeHandler} />
                {this.validator.message('password', this.state.password, 'required')}
              </div>
              <div className="form-group text-center">
                <label className="checkbox-inline"><input type="checkbox" /> Remember me</label>
              </div>
              <div className="form-group">
                <button type="submit" className="btn btn-primary btn-block">Log in</button>
              </div>
              <p className="text-center">Don't have an account? <a href="/registration">Register</a></p>
              <p className="text-center"><a href="/forgot-password">Forgot Password?</a></p>
            </form>
          </div>
        </div>


        <Footer />

        <Modal show={this.state.showModal} onHide={this.closeModal} animation={false}>
          <Modal.Header closeButton>
            <Modal.Title>Login</Modal.Title>
          </Modal.Header>
          <Modal.Body>{this.state.errorMessage}</Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={this.closeModal}>
              Ok !!!
            </Button>
          </Modal.Footer>
        </Modal>
      </div>

    );
  }
};

export default Login;