import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import configureStore from './store/configureStore';
import './index.css';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import App from './App';
import Contact from "./pages/contact";
import Order from "./pages/order";
import OrderPayment from "./pages/order-payment";
import Login from "./pages/login";
import Registration from "./pages/registration";
import ForgotPassword from "./pages/forgot-password";
import ResetPassword from "./pages/reset-password";
import ShowLoogedInUserDashboard from "./components/handleDashboard";
import QuoteDetails from "./pages/quote-details";
import * as serviceWorker from './serviceWorker';
import { Route, BrowserRouter as Router } from 'react-router-dom';
import ViewUsers from './pages/users';
import ManageUsers from './pages/manage-users';

const store = configureStore(window.REDUX_INITIAL_DATA);

const routing = (
  <Router>
    <div>
      <Route exact path="/" component={App} />
      <Route path="/contact" component={Contact} />
      <Route exact path="/order" component={Order} />
      <Route path="/order/payment" component={OrderPayment} />
      <Route path="/quote/details" component={QuoteDetails} />
      <Route path="/dashboard" component={ShowLoogedInUserDashboard} />
      <Route path="/login" component={Login} />
      <Route path="/registration" component={Registration} />
      <Route path="/forgot-password" component={ForgotPassword} />
      <Route path="/reset-password" component={ResetPassword} />
      <Route path="/manage-users" component={ManageUsers} />
      <Route path="/users" component={ViewUsers} />
    </div>
  </Router>
);

ReactDOM.render(<Provider store={store}>{routing}</Provider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
