import React from "react";
import "../css/label-button.css";
import '../css/quote.scss';
import SimpleReactValidator from 'simple-react-validator';
import { AppConfig } from '../GlobalConstants';
import axios from 'axios';
import { connect } from 'react-redux';

class Quote extends React.Component {
  constructor() {
    super();

    this.validator = new SimpleReactValidator({
      autoForceUpdate: this,
      className: 'text-danger'
    });
    this.deliveryClick = this.deliveryClick.bind(this);
    this.onSiteClick = this.onSiteClick.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.addressChangeHandler = this.addressChangeHandler.bind(this);
    this.getDistance = this.getDistance.bind(this);
    this.quantityChangeHandler = this.quantityChangeHandler.bind(this);
  }

  async getDistance() {
    const finalDieselPrice = this.props.quoteQuantity * AppConfig.DieselPrice;
    this.props.dispatch({
      type: "SET_QUOTE_FINAL_DIESEL_PRICE",
      payload: finalDieselPrice
    });
    if (this.props.quoteShowAddress) {
      await axios.post(`${AppConfig.ServerBaseUrl}/calculations/distance`, {
        address: this.props.quoteAddress
      }).then((response) => {
        this.props.dispatch({
          type: "SET_QUOTE_DISTANCE",
          payload: response.data.distance.replace(" km", "")
        });
        this.props.history.push(`/quote/details`);
      }).catch((error) => {
        console.log(error);
      });
    }
    else {
      this.props.history.push(`/quote/details`);
    }

  }

  quantityChangeHandler(event) {
    const quantity = event.target.value;
    this.props.dispatch({
      type: "SET_QUOTE_QUANTITY",
      payload: quantity
    });
  }

  addressChangeHandler(event) {
    const address = event.target.value;
    this.props.dispatch({
      type: "SET_QUOTE_ADDRESS",
      payload: address
    });
  }

  deliveryClick() {
    this.props.dispatch({
      type: "SET_QUOTE_SHOW_ADDRESS",
      payload: !this.props.quoteShowAddress
    });
  }

  onSiteClick() {
    this.props.dispatch({
      type: "SET_QUOTE_SHOW_ADDRESS",
      payload: false
    });
  }

  submitHandler = (event) => {
    event.preventDefault();
    if (this.validator.allValid()) {
      this.getDistance();
    } else {
      this.validator.showMessages();
    }
  };

  render() {
    return (
      <div>
        <div className="quote">
          <div className="card">
            <div class="card-header">
              <h4 className="text-left">Get A Quote</h4>
            </div>

            <div className="card-body">
              <form onSubmit={this.submitHandler}>
                <div className="form-group">
                  <input type="text" className="form-control" id="quantity" placeholder="Quantity (in litres)" value={this.props.quoteQuantity} onChange={this.quantityChangeHandler} />
                  {this.validator.message('quantity', this.props.quoteQuantity, 'required|numeric')}
                </div>
                <div className="form-group labelWithButtons">
                  <label>Order Method</label>
                  <div className="btn-toolbar buttonWrapper" role="toolbar" aria-label="Toolbar with button groups">
                    <div className="btn-group mr-2" role="group" aria-label="First group">
                      <button type="button" className="btn" onClick={this.onSiteClick}>On Site Collection</button>
                    </div>
                    <div className="btn-group mr-2" role="group" aria-label="Second group">
                      <button type="button" className="btn" onClick={this.deliveryClick}>Delivery</button>
                    </div>
                  </div>
                </div>
                <div style={{ display: this.props.quoteShowAddress ? 'block' : 'none' }}>
                  <div className="form-group">
                    <input type="text" className="form-control" id="message" placeholder="Address" value={this.props.quoteAddress} onChange={this.addressChangeHandler} />
                    {this.props.quoteShowAddress ? this.validator.message('address', this.props.quoteAddress, 'required') : ""}
                  </div>
                </div>
                <div className="text-left">
                  <button type="submit" className="btn">Generate Quote</button></div>
              </form>
            </div>
          </div>
        </div>

      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    quoteShowAddress: state.quoteReducer.quoteShowAddress,
    quoteQuantity: state.quoteReducer.quoteQuantity,
    quoteAddress: state.quoteReducer.quoteAddress,
    quoteDistance: state.quoteReducer.quoteDistance,
    quoteFinalDieselPrice: state.quoteReducer.quoteFinalDieselPrice
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    dispatch
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Quote);